.footer-container {
    background-color: #242424;
    padding: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
  
/* Social Icons */
.social-icon-link {
    color: #fff;
    font-size: 24px;
}
  
.social-media {
    max-width: 1000px;
    width: 100%;
}
  
.social-media-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    
    max-width: 1000px;
    margin: 5px auto 0 auto;
}
  
.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70px;
}
  
.social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}
  
.website-rights {
    color: #fff;
    margin-left: 20px;
    margin-right: 20px;
    max-width: 200px;
}
  
@media screen and (max-width: 820px) {
    .footer-links {
      padding-top: 2rem;
    }
  
    .footer-input {
      width: 100%;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
    }
  
    .social-media-wrap {
      flex-direction: column;
    }
}
  
