video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
  
.general-container {
    /* background: url('/public/images/img-1.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}
    
.general-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}
  
.general-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
}
  
.general-btns {
    margin-top: 32px;
}
  
.general-btns .btn {
    margin: 6px;
}

.fa-mug-saucer, .fa-laptop-code, .fa-heart {
  margin-left: 0.5rem;
  font-size: 1.8rem;
  color: white;
}

@media screen and (max-width: 960px) {
    .general-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
}
  
@media screen and (max-width: 768px) {
    .general-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .general-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
}